<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="comapny-setup">
            <input type="hidden" asp-for="AssetCatalogueId" />
            <!-- <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
                        <div class="tab-pane call-detail  show active border-0 p-0" id="tabs-1" role="tabpanel"
                            aria-labelledby="CreateProduct-tab">
                            <div class="container-fluid border pt-2">
                                <div class="col-lg-12 mt-3 p-0">
                                    <div class="heading-border mb-2">
                                        <h5 class="h5">
                                            <span id="TabHeading1">{{ $t('CreateItem') }}</span>
                                        </h5>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div> -->
            <div class="border p-3">

                <dynamic-form :buttons="buttons" ref="updateValues" :schema="FormSchema" v-on:OnSubmit="onSubmit">

                    <template v-slot:tgslot-ItemCode="{ data }">

                        <div class="form-group">


                            <Field :rules="{ 'required': false }" class="w-100" name="ProductTypeCode"
                                v-slot="{ errors }">

                                <a href="javascript:;" data-toggle="tooltip" data-trigger="click" @click="toggleTip"
                                    title="Please enter the product code. Else six digit item code will be generated by system.">
                                    <i class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></i>
                                </a>

                                <div class="d-flex">
                                    <input type="text" name="ProductTypeCode" disabled
                                        v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                        v-model="data.fieldInfo.value">

                                </div>

                            </Field>


                        </div>



                    </template>

                </dynamic-form>
            </div>



        </div>
        <TagNamePopup v-if="IsShowAddNamePopup"></TagNamePopup>
    </section>
</template>
<script>
import DataService from '../../service/DataService'
import TagNamePopup from '../../components/Modal/TagNamePopup.vue';
export default {
    props: {
        AssetCatalogId: {
            type: String,
            required: true
        },

    },
    components: {
        TagNamePopup,
    },
    data() {
        return {
            FormSchema: [
                {
                    group_name: 'Create Product',
                    group_display_name: this.$t('CreateItem'),
                    layoutType: "four",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('ProductType'),
                            name: "ProductType",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,

                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ProductName'),
                            name: "ProductName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('BarcodeType'),
                            name: "BarcodeType",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Audit'),
                            name: "Audit",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ImageCapture'),
                            name: "ImageCapture",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Calibration'),
                            name: "Calibration",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('ItemCode'),
                            name: "ItemCode",
                            value: "",
                            placeholder: "",

                        },
                        {
                            astype: "FileField",
                            showLabel: true,
                            label: this.$t('DisplayImage'),
                            name: "DisplayImage",
                            value: "",
                            placeholder: "",
                            validationRules: "",
                            FieldIndex: 0,
                            config: {
                                maxSize: 5,
                                accept: [
                                    {
                                        fileType: 'png',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'jpeg',
                                        iconClass: 'fa-file-image-o'
                                    },
                                    {
                                        fileType: 'pdf',
                                        iconClass: 'fa-file-archive-o'
                                    }
                                ],

                                showSelectedFiles: [
                                    {
                                        attachmentId: "",
                                        attachmentName: "",
                                        attachmentPath: "",
                                    },
                                ],
                                onClickSelectedFiles: this.onClickSelectedFile,
                                onDeleteSelectedFiles: this.onDeleteSelectedFiles,
                                onChange: this.setFile,
                                showAddIcon: false,
                                onAddButtonClick: this.AddFileMore,
                                multiple: false
                            },
                        },
                        {
                            astype: "CustomSelectField",
                            label: this.$t('Tags'),
                            name: "Tags",
                            value: "",
                            mode: "tag",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: true,
                                onAddButtonClick: this.OpenAddTagNamePopup,
                            },
                        },
                    ]
                },
                {
                    layoutType: "Single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        placeholder: "",

                    },]
                },
                {
                    layoutType: "Single",
                    Data: [{
                        astype: "toggleField",
                        label: this.$t('CreateUniqueNameListing'),
                        name: "CreateUniqueNameListing",
                        value: "",
                        placeholder: "",
                        visibility: false,
                    },]
                },
                {
                    layoutType: "double",
                    group_name: 'Minimum Stock For Alert/Location',
                    group_display_name: this.$t('Minimum_Stock_For_Alert'),
                    Data: [
                        {
                            astype: "NumericField",
                            label: this.$t('ForCompany'),
                            name: "ForCompany",
                            value: "",
                            placeholder: "",

                        },
                        {
                            astype: "NumericField",
                            label: this.$t('ForEmployee'),
                            name: "ForEmployee",
                            value: "",
                            placeholder: "",

                        },
                    ]
                },
            ],
            buttons: [
                {

                    type: "submit",
                    class: "btn btn-primary HideTab",
                    text: "Next<i class='fa fa-angle-double-right pl-2'></i>",
                    name: 'Next'
                    //onClick:this.btnNextClick

                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    name: 'Save',
                    isVisible: true,


                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    isVisible: true,
                    onClick: this.btnCancelClick
                }



            ],
            AssetType: [],
            BarcodeType: [],
            StatusList: [],
            AssetFrequencyList: [],
            IsShowAddNamePopup: false,
            AssetCatalogDataByID: [],
            isLoading: false,
            IsEnableAssetManagement: false,
            fileUploaded: '',
            TagsArray: [],
            DataLoaded: 0,
            TagsSelectedArray: [],
            tagsData: [],
            TagValues: [],
            file: []
        }
    },
    async created() {
        var vm = this;
        await vm.GetAssetType();
        await vm.GetBarcodeType();
        await vm.GetStatusList();
        await vm.GetAssetFrequency();
        await vm.EnableAssetManagement();
        await vm.GetTagsByModuleI();
        await vm.GetAssetTagsById();
        if (window.location.href.split('/')[5] != undefined) {
            vm.AssetCatalogId = window.location.href.split('/')[5];
            vm.FormSchema[2].Data[0].visibility = false;
            await vm.GetAssetCatalogById();
        }
        else {
            vm.AssetCatalogId = 0
            vm.FormSchema[2].Data[0].visibility = true;
        }
        vm.DataLoaded = 1;
    },
    methods:

    {
        onDeleteSelectedFiles: function (itm) {
            var vm = this;           
            var params = `attachmentId=${itm.attachmentId}&statusId=1003`;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("File")), function () {
                vm.$delete(vm.FormSchema[0].Data[8].config, 'showSelectedFiles');
                DataService.DeleteFile(params).then(response => {
                    if (response.data == true) {
                        vm.ShowAlert(vm.$t("FileDeleted"), "success", vm.$t('Alert'));
                        vm.FetchData();
                    }
                    else {
                        vm.ShowAlert(vm.$t("Unknown  Error"), "failure", vm.$t('Alert'));
                    }
                })
            })            
        },
        onClickSelectedFile(itm) {
            var vm = this;
            vm.isLoading = true;
            var attachmentFilePath = vm.AssetCatalogDataByID.ATTACHMENT_PATH.split('/')[4] + '/' + vm.AssetCatalogDataByID.ATTACHMENT_PATH.split('/')[5] + '/' + vm.AssetCatalogDataByID.ATTACHMENT_PATH.split('/')[6] + '/' + vm.AssetCatalogDataByID.ATTACHMENT_PATH.split('/')[7] + '/' + vm.AssetCatalogDataByID.ATTACHMENT_PATH.split('/')[8] + '/' + vm.AssetCatalogDataByID.ATTACHMENT_PATH.split('/')[9];
            var fileType = vm.AssetCatalogDataByID.ATTACHMENT_PATH.split('.')[5];
            var attachmentName = vm.AssetCatalogDataByID.ATTACHMENT_NAME;
            var param = `filePath=${attachmentFilePath}`;
            // var url = `?fileId=${item.RelatedInfoId}&filePath=${item.FilePath}&fileName=${item.AttachmentName}&FileType=${item.FileType}`;
            DataService.DownloadAttachment(param)
                .then(response => {
                    if (response.data != null && response.status === 200) {
                        const byteData = response.data;
                        const fileURL = `data:${fileType};base64,${byteData}`;
                        var fileLink = document.createElement('a');
                        fileLink.href = fileURL;
                        fileLink.setAttribute('download', attachmentName);
                        document.body.appendChild(fileLink);
                        fileLink.click();
                        vm.isLoading = false;
                    }
                    else {
                        vm.isLoading = false;
                    }
                });
        },
        btnNextClick() {
            var vm = this;
            vm.$parent.btnNextTab()
        },
        btnCancelClick() {
            var vm = this;
            vm.$parent.btnCancel();
        },
        setFile: function (e) {
            var vm = this;
            for (let index = 0; index < e.target.files.length; index++) {
                const element = e.target.files[index];
                vm.file.push(element)
            }
        },
        onSubmit: function (formfields, val) {
            var vm = this;
            if (vm.IsEnableAssetManagement) {
                let myObj = {};
                let obj1 = {};
                myObj.AssetCatalogueId = vm.AssetCatalogId;
                myObj.Name = formfields.ProductName,
                    myObj.LocationId = vm.LocationId == undefined ? '-1' : vm.LocationId;
                myObj.AssetTypeId = formfields.ProductType.toString(),
                    myObj.BarcodeTypeId = formfields.BarcodeType,
                    myObj.StatusId = formfields.Status.toString(),
                    myObj.AssetTagIdsWizard = Array.prototype.map.call(formfields.Tags, function (item) { return item.value; }).join(",");
                myObj.CreateUniqueNameListing = formfields.CreateUniqueNameListing == true ? 1 : 0,
                    myObj.TotalCost = "0",
                    myObj.AssetCost = "0",
                    myObj.DeployId = "0";
                myObj.DeployProduct = false;
                myObj.Quantity = "0";
                myObj.UnitPrice = "0";
                myObj.ParentUniqueId = "",
                    myObj.ChildUniqueId = "",
                    myObj.ChildCatalogueId = "",
                    myObj.CatalogueId = "",
                    myObj.CatalogHdnId = "",
                    myObj.SelfieEnabled = "",
                    myObj.ChildCategoryName = "",
                    myObj.AssetUniqueName = "",
                    myObj.AssetCatalogName = "",
                    myObj.Description = formfields.Description,

                    myObj.AssetTypeTagIdsWizard = "",
                    myObj.Manufacturer = "",
                    myObj.NumberOfItems = "0",
                    myObj.AsseetGroupQuantity = "",
                    myObj.ItemCode = "",
                    myObj.ChildItemCode = "",
                    myObj.ChildCatalogName = "",
                    myObj.ChildRequestQuantity = "",
                    myObj.ChildAssetType = "",
                    myObj.AssetType = "",
                    myObj.StandardTypeId = "",
                    myObj.AssetCompartmentId = "",
                    myObj.AssetGroupId = "",
                    myObj.IsSelfCheckout = "",
                    myObj.IsEnableTerms = "",
                    myObj.SubLocationId = "",
                    myObj.Attachment = "",
                    myObj.CreatedAt = "",
                    myObj.CreatedBy = "",
                    myObj.ModifiedAt = "",
                    myObj.ModifiedBy = "",
                    myObj.Asset_Type = "",
                    myObj.Status = "",
                    myObj.ForCompany = formfields.ForCompany,
                    myObj.ForEmployee = formfields.ForEmployee,
                    myObj.MoveToInTransitInfo = "",
                    myObj.AvailableItems = "",
                    myObj.PendingAvailableQuantity = "",
                    myObj.PendingTotalAvailableItems = "",
                    myObj.TotalQuantity = "",
                    myObj.ChildAvailableItems = "",
                    myObj.TotalFilesCount = "",
                    myObj.AssetTypeCategory = "",
                    myObj.AssetTypeCategoryName = "",
                    myObj.AssetTypeCategoryCode = "",
                    myObj.AttachmentName = "",
                    myObj.DisplayImage = "",
                    myObj.IsTermsAndCondition = "",
                    myObj.TotalTermsConditionFilesCount = "",
                    myObj.RelatedInfoId = "",
                    myObj.UniqueName = formfields.CreateUniqueNameListing == true ? 1 : 0,
                    myObj.AssetCatalogueGroup = "",
                    myObj.ChildCreateUniqueNameListing = "",
                    myObj.IsTransitItems = "",
                    myObj.AttachmentPath = "",
                    myObj.LocationName = "",
                    myObj.SubLocationName = "",
                    myObj.AuditFrequency = formfields.Audit == "" ? "" : formfields.Audit,
                    myObj.ImageCaptureFrequency = formfields.ImageCapture == "" ? "" : formfields.ImageCapture,
                    myObj.CalibrationFrequency = formfields.Calibration == "" ? "" : formfields.Calibration,
                    myObj.CR = "",
                    myObj.DR = "",
                    myObj.IsDisplayImage = "",
                    myObj.PurchaseValue = "",
                    myObj.CurrentBookValue = "",
                    myObj.SalvageValue = "",
                    myObj.Unit = "",
                    myObj.ReqFrom = "",
                    myObj.IsContainer = "",
                    myObj.IsSampleData = "",
                    myObj.Model = "",
                    myObj.Vendor = "",
                    myObj.ManufacturerId = "",
                    myObj.PurchaseOrder = "",
                    myObj.WarrantyDuration = "",
                    myObj.DepreciationRuleId = "",
                    myObj.OrderDate = "",
                    myObj.SalvageCost = "",
                    myObj.ItemLife = "",
                    myObj.InvoiceNumber = "",
                    myObj.InvoiceDate = "",
                    myObj.InsurerName = "",
                    myObj.InsuranceRefNumber = "",
                    myObj.InsuranceTo = "",
                    myObj.DeployedBy = "",
                    myObj.DeployedAt = "",
                    myObj.EnableWarranty = "",
                    myObj.EnableInsurance = "",
                    myObj.EnableItemCost = "",
                    myObj.EnableCalibration = "",
                    myObj.EnableDepreciation = "";

                var formData = new FormData();
                var postString = { "postString": myObj }

                formData.append("postString", JSON.stringify(myObj));
                if (formfields.DisplayImage != null) {
                    formData.append("Files", formfields.DisplayImage[0]);
                }
                formData.append("IsTermsAndCondition", false);
                formData.append("Is_DisplayImage", 'Yes');
                DataService.AddAssetWizard(formData).then(function (response) {
                    if (response.data.msg == "size_exceed" || response.data.msg == "FileSizeExceeded" || response.data.msg == "ExceedAllowedSize") {
                        vm.ShowAlert(vm.$t('FileSizeExceedMsg'), "failure", true, vm.$t("Alert"))
                    }
                    else if (response.data.msg == 'alreadyexist') {
                        vm.ShowAlert(vm.$t("Already exist record!"), "warning", vm.$t('Alert'));
                    }
                    else if (response.data.msg == 'ValidFileUploadMsg') {
                        vm.ShowAlert(vm.$t("Please upload valid file!"), "warning", vm.$t('Alert'));
                    }
                    else if (response.data.msg == 'add') {
                        if (val == "Next") {
                            var recId = response.data.recId;
                            vm.$router.push({
                                path: '/AssetCatalog/Update/' + recId
                            });
                            vm.$parent.btnNextTab(recId)
                        }

                        else {
                            vm.ShowAlert(vm.$t("Product has been updated Successfully"), "success", vm.$t('Alert'));
                            vm.$router.push({ path: '/AssetCatalog/Index' });
                        }
                    }
                    else {
                        vm.ShowAlert((vm.$t('Unknown Error'), "faliure", vm.$t("Alert")))
                    }
                })
            }

            else {
                vm.ShowAlert((vm.$t('UnauthorizedAccess'), "faliure", true, vm.$t("Alert")))
            }

        },
        async GetTagsByModuleI() {

            var vm = this;
            await DataService.GetTagsByModuleId('moduleName=Asset').then(function (response) {
                vm.TagsArray = JSON.parse(response.data).data;
                vm.TagsArray.forEach((itm, index) => {
                    vm.FormSchema[0].Data[9].config.options.push({
                        name: itm.TAG_NAME,
                        value: itm.TAG_ID
                    })
                })
            })
        },
        // UploadFile(event) {

        //     var vm = this;
        //     vm.fileUploaded = event.target.files[0];
        // },
        EnableAssetManagement: function () {
            let url = `moduleName=Asset&configkey=CanSeeAssetManagement`;
            DataService.EnableAssetManagement(url).then(response => {
                this.IsEnableAssetManagement = response.data;

            });
        },

        async GetAssetTagsById() {
            var vm = this;
            var params = `id=${vm.AssetCatalogId}&is_for=ASSET_CATALOGUE`;
            await DataService.GetAssetTagsById(params).then(response => {

                vm.tagsData = JSON.parse(response.data)
                vm.tagsData.forEach((item, index) => {
                    vm.TagsSelectedArray.push({
                        label: item.TAG_NAME,
                        value: item.TAG_ID
                    })
                })
                vm.FormSchema[0].Data[9].value = vm.TagsSelectedArray;
            })
        },
        GetAssetCatalogById() {
            var vm = this;
            vm.isLoading = true;
            var url = `id=${vm.AssetCatalogId}`;
            DataService.GetById(url).then(response => {
                vm.AssetCatalogDataByID = JSON.parse(response.data.result).data[0];
                vm.FormSchema[0].Data[0].value = vm.AssetCatalogDataByID.ASSET_TYPE_ID;
                vm.FormSchema[0].Data[1].value = vm.AssetCatalogDataByID.NAME;
                vm.FormSchema[0].Data[2].value = vm.AssetCatalogDataByID.BARCODE_TYPE_ID;
                vm.FormSchema[0].Data[7].value = vm.AssetCatalogDataByID.ITEM_CODE;
                vm.FormSchema[0].Data[6].value = vm.AssetCatalogDataByID.CALIBRATION_FREQUENCY == null ? "" : vm.AssetCatalogDataByID.CALIBRATION_FREQUENCY;
                vm.FormSchema[0].Data[5].value = vm.AssetCatalogDataByID.IMAGE_CAPTURE_FREQUENCY == null ? "" : vm.AssetCatalogDataByID.IMAGE_CAPTURE_FREQUENCY;
                vm.FormSchema[0].Data[4].value = vm.AssetCatalogDataByID.AUDIT_FREQUENCY == null ? "" : vm.AssetCatalogDataByID.AUDIT_FREQUENCY;
                vm.FormSchema[0].Data[3].value = vm.AssetCatalogDataByID.STATUS_ID;
                vm.LocationId = vm.AssetCatalogDataByID.LOCATION_ID;
                vm.FormSchema[1].Data[0].value = vm.AssetCatalogDataByID.DESCRIPTION;
                vm.FormSchema[3].Data[0].value = vm.AssetCatalogDataByID.MIN_CMP_QUANTITY;
                vm.FormSchema[3].Data[1].value = vm.AssetCatalogDataByID.MIN_EMP_QUANTITY;
                if (vm.AssetCatalogDataByID.ATTACHMENT_PATH != null && vm.AssetCatalogDataByID.ATTACHMENT_PATH != '') {                    
                    vm.FormSchema[0].Data[8].config.showSelectedFiles[0].attachmentId=vm.AssetCatalogDataByID.ATTACHMENT_ID,
                    vm.FormSchema[0].Data[8].config.showSelectedFiles[0].attachmentPath = vm.AssetCatalogDataByID.ATTACHMENT_PATH;
                    vm.FormSchema[0].Data[8].config.showSelectedFiles[0].attachmentName = vm.AssetCatalogDataByID.ATTACHMENT_PATH.split('/')[7];
                }
                else {
                    delete vm.FormSchema[0].Data[8].config.showSelectedFiles;
                }

                vm.isLoading = false;
            })
        },
        async GetAssetType() {
            var vm = this;
            vm.isLoading = true;
            var url = `type=ITEMCODE&issueTo=&locationId=`;
            await DataService.GetAssetTypeCustom(url).then(response => {
                vm.AssetType = JSON.parse(response.data);

                vm.AssetType.forEach((itm) => {
                    vm.FormSchema[0].Data[0].config.options.push({
                        name: itm.ASSETTYPE,
                        value: itm.ASSETID
                    })
                })
                vm.isLoading = false;
            })
        },
        async GetBarcodeType() {
            var vm = this;
            vm.isLoading = true;
            var url = `BarcodeTypeId=`
            await DataService.GetBarcodetype(url).then(response => {
                vm.BarcodeType = JSON.parse(response.data.result);
                vm.BarcodeType.forEach((itm, index) => {
                    (vm.FormSchema)[0].Data[2].config.options.push({
                        name: itm.BARCODE_TYPE,
                        value: itm.BARCODE_TYPE_ID
                    })
                })
                vm.isLoading = false;
            })
        },
        async GetStatusList() {

            var vm = this;
            vm.isLoading = true;
            var url = `langCode=en&statusType=AssetManagement&hasglobal=true`
            await DataService.GetStatusList(url).then(response => {
                vm.StatusList = JSON.parse(response.data);
                vm.StatusList.forEach((item) => {
                    vm.FormSchema[0].Data[3].config.options.push({
                        name: item.STATUS,
                        value: item.STATUS_ID

                    })
                })
                vm.isLoading = false;
            })
        },
        GetAssetFrequency() {

            var vm = this;
            let obj1 = [
                {
                    name: 'Annually',
                    value: 'annual'
                },
                {
                    name: 'Half yearly',
                    value: 'halfyear'
                },
                {
                    name: 'Quarterly',
                    value: 'quarterly'
                },
                {
                    name: 'Monthly',
                    value: 'month'
                },
                {
                    name: 'Bi-Weekly',
                    value: 'Bi-Weekly'
                },
                {
                    name: 'Weekly',
                    value: 'week'
                },
                {
                    name: 'Daily',
                    value: 'daily'
                },
            ]

            vm.AssetFrequencyList.push(obj1);


            vm.AssetFrequencyList[0].forEach((item, index) => {
                vm.FormSchema[0].Data[4].config.options.push({
                    name: item.name,
                    value: item.value

                })
                vm.FormSchema[0].Data[5].config.options.push({
                    name: item.name,
                    value: item.value

                })
                vm.FormSchema[0].Data[6].config.options.push({
                    name: item.name,
                    value: item.value

                })
            })

            this.$refs.updateValues.UpdateKeyValue();


        },
        OpenAddTagNamePopup() {
            var vm = this;
            vm.IsShowAddNamePopup = true;
        },
        CloseTagPopup() {
            var vm = this;
            vm.IsShowAddNamePopup = false;
        },
        toggleTip() {
            $('[data-toggle="tooltip"]').tooltip({
                trigger: 'click'
            }).on('shown.bs.tooltip', function () {
                var $this = $(this);
                setTimeout(function () {
                    $this.tooltip('hide');
                }, 2000);
            });

        },
    }

}


</script>

<style scoped>
#ItemCode .form-group a {
    position: absolute;
    top: -24px;
    left: 105px;
}
</style>